#cover-image {
  width: 100%;
  animation: fadeIn 2s;
  height: 100vh;
  margin-top: -1rem;
}

.cover-image-container {
  position: relative;
}

.cover-image-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: fadeIn 1s;
  border: 1px solid #fec601ff;
  border-radius: 10px;
  padding: 2rem 5rem;
  background: rgba(34, 37, 40, 0.823);
  color: #f8f9faff;
}

.cover-image-text a {
  color: #fec601ff;
  text-decoration: none;
}

@media (max-width: 500px) {
  .cover-image-text {
    position: relative;
    transform: none;
    top: 0;
    left: 0;
    margin-top: 10rem;
  }

  #cover-image {
    display: none;
  }
}
